.form-container {
    margin: 100px auto;
    margin-top: 0;
  
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);
  
    border-radius: 10px;
    
    display: flex;
    flex-direction: row;
    
  }
  
  .form-content-left {
    /* background: linear-gradient(
      90deg,
      rgb(39, 176, 255) 0%,
      rgb(0, 232, 236) 100%
    ); */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 25px;
    margin: 1rem;
    flex:1;
  }
  
  .form-img {
    margin: 1rem;
    /* width: 80%; */
    border-radius: 25px;
    width: 100%;
  }
  
  .form-img-2 {
    width: 60%;
    margin: 1rem;
  
  }
  
  .form-success {
    text-align: center;
    font-size: 24px;
    margin-top: 80px;
    color: #fff;
  }
  
  .form-content-right {
    border-radius: 0 10px 10px 0;
    background-color: black;
    display: flex;
    flex-direction:column;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex:1;
  }
  
  .form {
  
  
  
  
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form h1 {
    font-size: 1rem;
    text-align: start;
  
    margin-bottom: 1rem;
    color: #fff;
  }
  
  .form-inputs {
    margin-bottom: 0.5rem;
    width: 100%;
  
  }
  
  .form-inputs p {
    font-size: 0.8rem;
    margin-top: 0.5rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 0.8rem;
    margin-bottom: 6px;
    color: lightcyan;
  }
  
  .form-input {
    display: block;
    padding-left: 10px;
    outline: none;
    border-radius: 2px;
    height: 40px;
    width: 100%;
    border: none;
  }
  
  .form-input::placeholder {
    color: #595959;
    font-size: 15px;
  }
  
  .form-input-btn {
  
    height: 50px;
    margin-top: 1rem;
    border-radius: 2px;
    background: linear-gradient(
      90deg,
      rgb(39, 176, 255) 0%,
      rgb(0, 232, 236) 100%
    );
    outline: none;
    border: none;
    color: #fff;
    font-size: 1rem;
    width:100%;
  }
  
  .form-input-btn:hover {
    cursor: pointer;
    background: linear-gradient(
      90deg,
      rgb(39, 143, 255) 0%,
      rgb(12, 99, 250) 100%
    );
    transition: all 0.4s ease-out;
  }
  
  .form-input-btn{
    outline: none;
    border: none;
  }
  
  .form-input-submit {
    font-size: 0.8rem;
    margin-top: 10px;
    color: #fff;
  
    text-align: center;
  }
  
  .form-input-login a {
    text-decoration: none;
    color: #27cdff;
  
  }
  
  .form-textarea-input{
    font-weight: 15px;
    text-decoration: none;
    max-height: 10rem;
    width: 100%;
  }
  
  .form-contact-heading{
    color: white;
  }
  
  @media(max-width: 900px){
    .form-container {
      margin: 100px auto;
      margin-top: 0;
    
      box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      
      display: flex;
      flex-direction: column;
      
    }
  }

  .failed-para{
    color: red;
  }